"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import axiosInstance from "../../../../ultis/axios";
import { getStartOfWeekUTC, addDays } from "@/lib/timeline";
import ResponsiveSidebar from "@/components/ResponsiveSidebar";
import GanttChart from "@/components/GanttChart";

type Milestone = {
  _id: string;
  title: string;
  start_date?: string;
  deadline?: string;
  status?: string;
  description?: string;
  createdAt?: string;
};

export default function ProjectDetailPage() {
  const router = useRouter();
  const params = useParams();
  const projectId = Array.isArray(params?.id) ? params?.id[0] : (params?.id as string);
  const [milestones, setMilestones] = useState<Milestone[] | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!projectId) return;
    (async () => {
      try {
        const res = await axiosInstance.get(`/api/projects/${projectId}/milestones`);
        console.log(res.data);
        setMilestones(Array.isArray(res.data) ? res.data : []);
      } catch (e: any) {
        setError(e?.response?.data?.message || 'Không thể tải milestone');
      } finally {
        setLoading(false);
      }
    })();
  }, [projectId]);

  return (
    <div className="min-h-screen bg-[var(--background)]">
      <ResponsiveSidebar />
      <main className="p-4 md:p-6 md:ml-64">
        <div className="mx-auto w-full max-w-6xl">
          <div className="mb-6 md:mb-8 flex items-center justify-between">
            <h1 className="text-2xl md:text-3xl font-semibold tracking-tight" style={{color:'var(--primary)'}}>Milestones</h1>
            <div className="flex items-center gap-2">
              <button onClick={() => router.push(`/projects/${projectId}/milestones/new`)} className="btn-primary rounded-lg px-4 py-2">+ Thêm Milestone</button>
              <button onClick={() => router.back()} className="px-3 py-2 rounded-lg text-sm hover:bg-[var(--muted)]" style={{border:'1px solid var(--border)'}}>Quay lại</button>
            </div>
          </div>

          {loading ? (
            <div>Đang tải...</div>
          ) : error ? (
            <div className="text-red-600">{error}</div>
          ) : (
            <Timeline milestones={milestones || []} projectId={projectId} onLocalUpdate={setMilestones as any} />
          )}
        </div>
      </main>
    </div>
  );
}

function Timeline({ milestones, projectId, onLocalUpdate }: { milestones: Milestone[]; projectId: string; onLocalUpdate: any }) {
  const [weekStart, setWeekStart] = useState<Date>(getStartOfWeekUTC(new Date()));
  const [viewMode, setViewMode] = useState<'Days' | 'Weeks' | 'Months' | 'Quarters'>('Days');
  const [autoFit, setAutoFit] = useState<boolean>(true);
  const [statusFilter, setStatusFilter] = useState<Record<string, boolean>>({
    Planned: true,
    'In Progress': true,
    Completed: true,
    Overdue: true,
  });
  if (!milestones || milestones.length === 0) {
    return <div className="opacity-70">Chưa có milestone nào.</div>;
  }

  return (
    <div className="w-full">
      <div className="flex flex-wrap items-center gap-2 mb-3">
        <select value={viewMode} onChange={(e) => setViewMode(e.target.value as any)} className="ml-0 px-2 py-1 rounded border text-sm" style={{borderColor:'var(--border)'}}>
          <option>Days</option>
          <option>Weeks</option>
          <option>Months</option>
          <option>Quarters</option>
        </select>
        <label className="ml-2 flex items-center gap-1 text-sm">
          <input type="checkbox" checked={autoFit} onChange={(e)=>setAutoFit(e.target.checked)} /> Auto Fit
        </label>
        <div className="flex items-center gap-3 ml-4 text-sm">
          {['Planned','In Progress','Completed','Overdue'].map(s => (
            <label key={s} className="flex items-center gap-1">
              <input
                type="checkbox"
                checked={!!statusFilter[s]}
                onChange={(e)=> setStatusFilter(prev => ({...prev, [s]: e.target.checked}))}
              /> {s}
            </label>
          ))}
        </div>
      </div>
      <GanttChart
        milestones={(milestones || []).filter(m => m.status ? statusFilter[m.status] : true)}
        viewMode={viewMode as any}
        startDate={weekStart}
        autoFit={autoFit}
        pagingStepDays={viewMode === 'Quarters' ? 90 : viewMode === 'Months' ? 30 : viewMode === 'Weeks' ? 7 : 7}
        onRequestShift={(days) => setWeekStart(prev => addDays(prev, days))}
        onMilestoneShift={(id, deltaDays) => {
          // Local optimistic update: shift start_date and deadline by deltaDays
          onLocalUpdate((prev: Milestone[]) => {
            const shiftDate = (iso?: string) => {
              if (!iso) return iso;
              const d = new Date(iso);
              d.setUTCDate(d.getUTCDate() + deltaDays);
              return d.toISOString();
            };
            return (prev || []).map((m) => m._id === id ? ({
              ...m,
              start_date: shiftDate(m.start_date),
              deadline: shiftDate(m.deadline),
            }) : m);
          });
        }}
      />
    </div>
  );
}

