"use client";

import React, { useEffect, useMemo, useRef, useState } from "react";
import { getSpanDays, getPeriodStart, stripTimeUTC } from "@/lib/timeline";

export type GanttMilestone = {
  _id: string;
  title: string;
  start_date?: string;
  deadline?: string;
  status?: string;
  description?: string;
  createdAt?: string;
};

type ViewMode = "Days" | "Weeks" | "Months" | "Quarters";

export default function GanttChart({
  milestones,
  viewMode,
  startDate,
  autoFit = true,
  pagingStepDays,
  onRequestShift,
  columnWidth = 160,
  enableDrag = true,
  enableWheelPan = true,
  enableEdgePaging = true,
  stickyScrollbar = true,
  statusColors,
  onMilestoneShift,
}: {
  milestones: GanttMilestone[];
  viewMode: ViewMode;
  startDate: Date;
  autoFit?: boolean;
  pagingStepDays?: number;
  onRequestShift?: (days: number) => void;
  columnWidth?: number;
  enableDrag?: boolean;
  enableWheelPan?: boolean;
  enableEdgePaging?: boolean;
  stickyScrollbar?: boolean;
  statusColors?: Partial<Record<string, string>>; // status -> tailwind/className
  onMilestoneShift?: (id: string, deltaDays: number) => void;
}) {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const bottomScrollRef = useRef<HTMLDivElement | null>(null);
  const isSyncingRef = useRef(false);
  const periodStart = useMemo(() => getPeriodStart(viewMode, startDate), [viewMode, startDate]);
  const spanDays = useMemo(() => getSpanDays(viewMode, startDate), [viewMode, startDate]);
  const isQuarters = viewMode === "Quarters";
  const days = useMemo(
    () => Array.from({ length: spanDays }, (_, i) => new Date(periodStart.getTime() + i * 24 * 60 * 60 * 1000)),
    [spanDays, periodStart]
  );
  const today = stripTimeUTC(new Date());

  const contentWidthPx = useMemo(() => (isQuarters ? 4 : spanDays) * columnWidth, [isQuarters, spanDays, columnWidth]);
  const isDraggingRef = useRef(false);
  const dragStartXRef = useRef(0);
  const dragStartScrollRef = useRef(0);
  const barDragStartXRef = useRef(0);
  const [barDragState, setBarDragState] = useState<{ id: string; delta: number } | null>(null);

  useEffect(() => {
    // Keep scroll positions aligned when dimensions change
    const c = containerRef.current;
    const b = bottomScrollRef.current;
    if (!c || !b) return;
    b.scrollLeft = c.scrollLeft;
  }, [contentWidthPx, viewMode, spanDays]);

  const getX = (dateStr?: string) => {
    if (!dateStr) return 0;
    const d = new Date(dateStr);
    const diff = Math.floor((stripTimeUTC(d).getTime() - stripTimeUTC(periodStart).getTime()) / (24 * 60 * 60 * 1000));
    return Math.max(0, Math.min(spanDays - 1, diff));
  };

  const getISOWeek = (d: Date) => {
    const date = new Date(Date.UTC(d.getUTCFullYear(), d.getUTCMonth(), d.getUTCDate()));
    const dayNum = date.getUTCDay() || 7; // ISO: Monday=1, Sunday=7
    date.setUTCDate(date.getUTCDate() + 4 - dayNum);
    const yearStart = new Date(Date.UTC(date.getUTCFullYear(), 0, 1));
    const weekNo = Math.ceil(((date.getTime() - yearStart.getTime()) / 86400000 + 1) / 7);
    return weekNo;
  };

  const weekHeader = useMemo(() => {
    const weekStart = days[0];
    const weekEnd = days[Math.min(days.length - 1, 6)];
    const isoWeek = getISOWeek(weekStart);
    const startLabel = `${weekStart.toLocaleDateString("en-US", { month: "short" })} ${weekStart.getUTCDate()}`;
    const endLabel = `${weekEnd.toLocaleDateString("en-US", { month: "short" })} ${weekEnd.getUTCDate()}`;
    return { isoWeek, range: `${startLabel} - ${endLabel}` };
  }, [days]);

  return (
    <div className="w-full">
      <div className="flex items-end justify-between mb-2">
        <div className="flex items-center gap-2">
          <div className="text-lg font-semibold" style={{ color: "var(--primary)" }}>
            {`Week ${weekHeader.isoWeek}`}
          </div>
          {!isQuarters ? (
            <div className="text-sm opacity-70">{weekHeader.range}</div>
          ) : null}
        </div>
        <div className="flex items-center gap-4 text-sm">
          <div className="flex items-center gap-1"><span className="h-3 w-3 rounded-full bg-amber-400 inline-block" /> Working on it</div>
          <div className="flex items-center gap-1"><span className="h-3 w-3 rounded-full bg-green-500 inline-block" /> Done</div>
          <div className="flex items-center gap-1"><span className="h-3 w-3 rounded-full bg-red-500 inline-block" /> Stuck</div>
        </div>
      </div>

      {viewMode === "Quarters" ? (
        <>
          <div className="text-center font-semibold mb-1" style={{ color: "var(--primary)" }}>
            {periodStart.getUTCFullYear()}
          </div>
          <div className="grid grid-cols-4 text-center text-xs md:text-sm font-medium mb-1 opacity-90">
            {["Q1", "Q2", "Q3", "Q4"].map((q, idx) => (
              <div key={q} className={`${Math.floor(periodStart.getUTCMonth() / 3) === idx ? "bg-blue-600 text-white rounded-full mx-auto px-3 py-1" : ""}`}>{q}</div>
            ))}
          </div>
        </>
      ) : (
        <div className="grid" style={{ gridTemplateColumns: `repeat(${spanDays}, minmax(0,1fr))` }}>
          {days.map((d, i) => {
            const isToday = stripTimeUTC(d).getTime() === stripTimeUTC(today).getTime();
            const weekdayIdx = d.getUTCDay();
            const short2 = ["Su","Mo","Tu","We","Th","Fr","Sa"][weekdayIdx];
            const label = `${short2} ${d.getUTCDate()}`;
            return (
              <div key={i} className="text-center font-medium text-xs md:text-sm mb-2 opacity-90">
                <span className={`${isToday ? "bg-blue-600 text-white px-3 py-1 rounded-full" : ""}`}>{label}</span>
              </div>
            );
          })}
        </div>
      )}

      <div
        ref={containerRef}
        className="relative rounded-lg select-none"
        style={{ border: "1px solid rgba(17,24,39,0.08)", overflowX: "hidden", overflowY: "hidden", background: "#ffffff" }}
        onScroll={(e) => {
          const b = bottomScrollRef.current;
          if (!b) return;
          if (isSyncingRef.current) return;
          isSyncingRef.current = true;
          b.scrollLeft = (e.currentTarget as HTMLDivElement).scrollLeft;
          isSyncingRef.current = false;

          // Edge paging when reaching bounds
          const c = e.currentTarget as HTMLDivElement;
          const max = c.scrollWidth - c.clientWidth;
          const nearStart = c.scrollLeft <= 2;
          const nearEnd = c.scrollLeft >= max - 2;
          if (pagingStepDays && onRequestShift) {
            if (nearEnd) {
              onRequestShift(pagingStepDays);
              // after parent shifts period, keep scroller at small offset
              setTimeout(() => {
                c.scrollLeft = 4;
                if (b) b.scrollLeft = c.scrollLeft;
              }, 0);
            } else if (nearStart) {
              onRequestShift(-pagingStepDays);
              setTimeout(() => {
                const newMax = c.scrollWidth - c.clientWidth;
                c.scrollLeft = newMax - 4;
                if (b) b.scrollLeft = c.scrollLeft;
              }, 0);
            }
          }
        }}
        onWheel={(e) => {
          if (!enableWheelPan) return;
          const c = containerRef.current;
          const b = bottomScrollRef.current;
          if (!c) return;
          // Convert vertical wheel to horizontal pan for convenience
          const delta = Math.abs(e.deltaX) > Math.abs(e.deltaY) ? e.deltaX : e.deltaY;
          c.scrollLeft += delta;
          if (b) b.scrollLeft = c.scrollLeft;

          // Check edges for paging
          const max = c.scrollWidth - c.clientWidth;
          const nearStart = c.scrollLeft <= 2;
          const nearEnd = c.scrollLeft >= max - 2;
          if (enableEdgePaging && pagingStepDays && onRequestShift) {
            if (nearEnd) {
              onRequestShift(pagingStepDays);
              setTimeout(() => {
                c.scrollLeft = 4;
                if (b) b.scrollLeft = c.scrollLeft;
              }, 0);
            } else if (nearStart) {
              onRequestShift(-pagingStepDays);
              setTimeout(() => {
                const newMax = c.scrollWidth - c.clientWidth;
                c.scrollLeft = newMax - 4;
                if (b) b.scrollLeft = c.scrollLeft;
              }, 0);
            }
          }
        }}
        onPointerDown={(e) => {
          if (!enableDrag) return;
          // If we're dragging a milestone bar, the bar's handler will set capture and stop propagation.
          if (barDragState) return;
          const c = containerRef.current;
          if (!c) return;
          isDraggingRef.current = true;
          dragStartXRef.current = e.clientX;
          dragStartScrollRef.current = c.scrollLeft;
          c.style.cursor = "grabbing";
          (e.currentTarget as HTMLElement).setPointerCapture(e.pointerId);
        }}
        onPointerMove={(e) => {
          if (!enableDrag) return;
          const c = containerRef.current;
          const b = bottomScrollRef.current;
          if (!c) return;
          // Handle bar drag if active
          if (barDragState) {
            const dx = e.clientX - barDragStartXRef.current;
            const deltaDays = Math.round(dx / columnWidth);
            if (deltaDays !== barDragState.delta) {
              setBarDragState({ id: barDragState.id, delta: deltaDays });
            }
            return;
          }
          if (!isDraggingRef.current) return;
          const dx = e.clientX - dragStartXRef.current;
          c.scrollLeft = dragStartScrollRef.current - dx;
          if (b) b.scrollLeft = c.scrollLeft;
        }}
        onPointerUp={(e) => {
          if (!enableDrag) return;
          const c = containerRef.current;
          if (!c) return;
          // Commit bar drag if active
          if (barDragState) {
            const delta = barDragState.delta || 0;
            if (delta !== 0 && !isQuarters) {
              onMilestoneShift?.(barDragState.id, delta);
            }
            setBarDragState(null);
            // Do not treat as a pan end
            return;
          }
          isDraggingRef.current = false;
          c.style.cursor = "default";
          (e.currentTarget as HTMLElement).releasePointerCapture(e.pointerId);
        }}
      >
        <div className="relative" style={{ width: `${contentWidthPx}px` }}>
          {!isQuarters ? (
            <div className={`absolute inset-0 grid pointer-events-none`} style={{ gridTemplateColumns: `repeat(${spanDays}, minmax(0, 1fr))` }}>
              {days.map((_, i) => (
                <div key={i} style={{ background: (i % 7 === 5 || i % 7 === 6) ? "rgba(17,24,39,0.03)" : "transparent" }} />
              ))}
            </div>
          ) : null}

          <div className="relative" style={{ display: "grid", gridTemplateColumns: `repeat(${isQuarters ? 4 : spanDays}, minmax(0,1fr))`, height: autoFit ? `${Math.max(180, (milestones.length * 44) + 40)}px` : "260px" }}>
            {(isQuarters ? Array.from({ length: 4 }) : days).map((_, i) => (
              <div key={i} className="relative last:border-r-0" style={{ borderRight: "1px solid rgba(17,24,39,0.08)" }}>
                <div className="absolute top-0 left-0 right-0" style={{ height: 1, background: "rgba(17,24,39,0.08)" }} />
              </div>
            ))}
          </div>

          {!isQuarters ? <TodayLine startOfPeriod={periodStart} today={today} /> : null}

          <div className="absolute inset-0 space-y-2">
            {milestones.map((m) => {
              const startCandidate = m.start_date || m.createdAt || m.deadline;
              const endCandidate = m.deadline || m.start_date;
              let styleObj: any;
              if (isQuarters) {
                const year = periodStart.getUTCFullYear();
                const qIndex = (d: Date) => Math.max(0, Math.min(3, d.getUTCFullYear() < year ? 0 : d.getUTCFullYear() > year ? 3 : Math.floor(d.getUTCMonth() / 3)));
                const sIdx = qIndex(new Date(startCandidate as string));
                const eIdx = qIndex(new Date((endCandidate as string) || (startCandidate as string)));
                const spanQ = Math.max(1, eIdx - sIdx + 1);
                styleObj = { marginLeft: `${(sIdx / 4) * 100}%`, width: `${(spanQ / 4) * 100}%` };
              } else {
                const start = getX(startCandidate);
                const end = getX(endCandidate);
                const span = Math.max(1, end - start + 1);
                styleObj = { marginLeft: `calc(${start}/${spanDays}*100%)`, width: `calc(${span}/${spanDays}*100%)` };
              }
              const defaultColor = m.status === "Completed"
                ? "bg-green-500/80 text-white"
                : m.status === "Overdue"
                ? "bg-red-500 text-white"
                : "bg-[#bfc3c9] text-white";
              const color = statusColors?.[m.status || ""] || defaultColor;
              const dateLabel = [m.start_date, m.deadline]
                .filter(Boolean)
                .map(d => new Date(d as string).toLocaleDateString("en-US"))
                .join(" → ");
              const isActive = !!barDragState && barDragState.id === m._id;
              const dragStyle = isActive && !isQuarters ? { transform: `translateX(${(barDragState!.delta) * columnWidth}px)` } : {};
              return (
                <div
                  key={m._id}
                  className="group relative flex items-center py-2"
                  style={{ ...styleObj, ...dragStyle }}
                  title={`${m.title} (${dateLabel})`}
                  onPointerDown={(e) => {
                    if (!enableDrag || isQuarters) return;
                    e.stopPropagation();
                    barDragStartXRef.current = e.clientX;
                    setBarDragState({ id: m._id, delta: 0 });
                    (e.currentTarget as HTMLElement).setPointerCapture(e.pointerId);
                  }}
                >
                  <div className={`h-8 w-full rounded-md text-sm px-3 flex items-center justify-between gap-2 ${color} shadow-sm hover:shadow-md transition-shadow cursor-grab`} style={{ overflow: "hidden" }}>
                    <span className="truncate">{m.title}</span>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      <div
        ref={bottomScrollRef}
        className="mt-2 w-full overflow-x-auto x-scrollbar"
        style={{ overflowY: "hidden", position: stickyScrollbar ? "sticky" as const : "static", bottom: stickyScrollbar ? 0 : undefined, background: "var(--background)", borderTop: "1px solid var(--border)", paddingTop: 2 }}
        onScroll={(e) => {
          const c = containerRef.current;
          if (!c) return;
          if (isSyncingRef.current) return;
          isSyncingRef.current = true;
          c.scrollLeft = (e.currentTarget as HTMLDivElement).scrollLeft;
          isSyncingRef.current = false;

          const b = e.currentTarget as HTMLDivElement;
          const max = b.scrollWidth - b.clientWidth;
          const nearStart = b.scrollLeft <= 2;
          const nearEnd = b.scrollLeft >= max - 2;
          if (enableEdgePaging && pagingStepDays && onRequestShift) {
            if (nearEnd) {
              onRequestShift(pagingStepDays);
              setTimeout(() => {
                b.scrollLeft = 4;
                c.scrollLeft = b.scrollLeft;
              }, 0);
            } else if (nearStart) {
              onRequestShift(-pagingStepDays);
              setTimeout(() => {
                const newMax = b.scrollWidth - b.clientWidth;
                b.scrollLeft = newMax - 4;
                c.scrollLeft = b.scrollLeft;
              }, 0);
            }
          }
        }}
      >
        <div style={{ width: `${contentWidthPx}px`, height: 1 }} />
      </div>
    </div>
  );
}

function TodayLine({ startOfPeriod, today }: { startOfPeriod: Date; today: Date }) {
  const daysFromStart = Math.max(0, Math.floor((stripTimeUTC(today).getTime() - stripTimeUTC(startOfPeriod).getTime()) / (24 * 60 * 60 * 1000)));
  const left = `calc(${daysFromStart}/var(--colCount,7)*100%)`;
  return (
    <div className="absolute inset-0 pointer-events-none">
      <div className="h-full border-l border-blue-500" style={{ marginLeft: left }} />
      <div className="relative" style={{ marginLeft: left }}>
        <span className="absolute -top-2 -ml-1 h-2 w-2 rounded-full bg-blue-500" />
      </div>
    </div>
  );
}


